<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="150px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="分享模板">
              <el-upload
                :limit="1"
                :action="'/api/api/upload?ssid=' + ssid"
                :on-success="fileSuccess"
                :file-list="file"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽850px 高:1280px 格式:png、jpg、jpeg
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="背景图">
              <el-upload
                :limit="1"
                :action="'/api/api/upload?ssid=' + ssid"
                :on-success="bgfileSuccess"
                :file-list="bgfile"
                list-type="picture"
              >
                <el-button size="small" type="infor">点击上传</el-button>
                <div slot="tip" style="color: red" class="el-upload__tip">
                  尺寸:宽850px 高:1280px 格式:png、jpg、jpeg
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="是否开启分享审核">
              <el-switch
                v-model="share_sh"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="打开审核"
                inactive-text="关闭审核"
                @change="shareHandle"
              >
              </el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <el-col>
        <el-form-item>
          <el-button type="primary" size="small" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      setting: {},
      file: [],
      bgfile: [],
      share_sh: true,
    }
  },
  mounted() {
    this.loading = true
    Fetch('/sys/config/info', {
      type: 'jz',
    })
      .then(r => {
        if (r.data.image) {
          this.file = [
            {
              name: r.data.image,
              url: r.data.image,
            },
          ]
        }
        if (r.data.bgimage) {
          this.bgfile = [
            {
              name: r.data.bgimage,
              url: r.data.bgimage,
            },
          ]
        }
        r.data.review == 1 ? (this.share_sh = true) : (this.share_sh = false)
        this.setting = r.data
        console.log('data', this.setting)
        this.loading = false
      })
      .catch(() => (this.loading = false))
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  methods: {
    onSubmit() {
      this.loading = true
      var sh = 2
      if (this.share_sh) {
        sh = 1
      } else {
        sh = 2
      }
      Fetch('/sys/config/update', {
        type: 'jz',
        ...this.setting,
        review: sh,
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
    fileSuccess(res) {
      console.log('res->', res)
      this.setting.image = res.data.file
    },
    bgfileSuccess(res) {
      console.log('resbg->', res)
      this.setting.bgimage = res.data.file
    },
    shareHandle(res) {
      if (res) {
        this.share_sh = true
      } else {
        this.share_sh = false
      }
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
